import React from 'react';
import { Container } from 'react-bootstrap';

export default function About(props) {
	let myStyle = {
		color: props.mode === 'dark' ? 'white' : 'black',
		backgroundColor: props.mode === 'dark' ? '#5e678d' : 'white',
	};

	return (
		<Container>
			<div>
				<h1 className='my-4' style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
					About Us
				</h1>
				<div className='accordion' id='accordionExample' style={myStyle}>
					<div className='accordion-item'>
						<h2 className='accordion-header' id='headingOne'>
							<button
								className='accordion-button'
								type='button'
								data-bs-toggle='collapse'
								data-bs-target='#collapseOne'
								aria-expanded='true'
								aria-controls='collapseOne'
								style={myStyle}
							>
								Accordion Item #1
							</button>
						</h2>
						<div
							id='collapseOne'
							className='accordion-collapse collapse show'
							style={myStyle}
							aria-labelledby='headingOne'
							data-bs-parent='#accordionExample'
						>
							<div className='accordion-body'>
								<strong>This is the first item's accordion body.</strong> It is shown by default, until the collapse plugin adds the appropriate
								classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS
								transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about
								any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
							</div>
						</div>
					</div>
					<div className='accordion-item'>
						<h2 className='accordion-header' id='headingTwo'>
							<button
								className='accordion-button collapsed'
								type='button'
								data-bs-toggle='collapse'
								data-bs-target='#collapseTwo'
								aria-expanded='false'
								aria-controls='collapseTwo'
								style={myStyle}
							>
								Accordion Item #2
							</button>
						</h2>
						<div
							id='collapseTwo'
							style={myStyle}
							className='accordion-collapse collapse'
							aria-labelledby='headingTwo'
							data-bs-parent='#accordionExample'
						>
							<div className='accordion-body'>
								<strong>This is the second item's accordion body.</strong> It is hidden by default, until the collapse plugin adds the appropriate
								classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS
								transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about
								any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
							</div>
						</div>
					</div>
					<div className='accordion-item'>
						<h2 className='accordion-header' id='headingThree'>
							<button
								className='accordion-button collapsed'
								type='button'
								data-bs-toggle='collapse'
								data-bs-target='#collapseThree'
								aria-expanded='false'
								aria-controls='collapseThree'
								style={myStyle}
							>
								Accordion Item #3
							</button>
						</h2>
						<div
							id='collapseThree'
							className='accordion-collapse collapse'
							style={myStyle}
							aria-labelledby='headingThree'
							data-bs-parent='#accordionExample'
						>
							<div className='accordion-body'>
								<strong>This is the third item's accordion body.</strong> It is hidden by default, until the collapse plugin adds the appropriate
								classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS
								transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about
								any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
							</div>
						</div>
					</div>
				</div>
			</div>
		</Container>
	);
}
