import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

function Navbar(props) {
	return (
		<nav className={`navbar navbar-expand-lg bg-${props.mode}`}>
			<div className='container-fluid'>
				<Link className={`navbar-brand text-${props.mode === 'dark' ? 'light' : 'black'}`} to='/'>
					{props.appTitle}
				</Link>
				<button
					className='navbar-toggler'
					type='button'
					data-bs-toggle='collapse'
					data-bs-target='#navbarSupportedContent'
					aria-controls='navbarSupportedContent'
					aria-expanded='false'
					aria-label='Toggle navigation'
				>
					<span className='navbar-toggler-icon'></span>
				</button>
				<div className='collapse navbar-collapse' id='navbarSupportedContent'>
					<ul className='navbar-nav me-auto mb-2 mb-lg-0 '>
						<li className='nav-item'>
							<Link className={`nav-link active text-${props.mode === 'dark' ? 'light' : 'black'}`} aria-current='page' to='/'>
								Home
							</Link>
						</li>
						<li className='nav-item'>
							<a className={`nav-link text-${props.mode === 'dark' ? 'light' : 'black'}`} href='/'>
								Link
							</a>
						</li>
						<li className='nav-item dropdown'>
							<a
								className={`nav-link dropdown-toggle text-${props.mode === 'dark' ? 'light' : 'black'}`}
								href='/'
								role='button'
								data-bs-toggle='dropdown'
								aria-expanded='false'
							>
								Dropdown
							</a>
							<ul className='dropdown-menu'>
								<li>
									<a className='dropdown-item ' href='/'>
										Action
									</a>
								</li>
								<li>
									<a className='dropdown-item' href='/'>
										Another action
									</a>
								</li>
							</ul>
						</li>
						<li className='nav-item'>
							<Link className={`nav-link text-${props.mode === 'dark' ? 'light' : 'black'}`} to='/about'>
								{props.aboutApp}
							</Link>
						</li>
						<li className='nav-item'>
							<a className={`nav-link disabled  text-${props.mode === 'dark' ? 'light' : 'black'}`} href='/'>
								Disabled
							</a>
						</li>
					</ul>
					<form className='d-flex ' role='search'>
						{/* <input className='form-control me-2 ' type='search' placeholder='Search' aria-label='Search' />
						<button className='btn btn-primary' type='submit'>
							Search
						</button> */}

						<div className={`form-check form-switch me-2 ms-5 text-${props.mode === 'dark' ? 'light' : 'black'}`}>
							<input className='form-check-input' type='checkbox' role='switch' id='flexSwitchCheckDefault' onClick={props.toggleMode} />
							<label className='form-check-label' htmlFor='flexSwitchCheckDefault'>
								Enable {props.mode === 'light' ? 'Dark' : 'Light'} Mode
							</label>
						</div>
					</form>
				</div>
			</div>
		</nav>
	);
}

Navbar.propTypes = { appTitle: PropTypes.string.isRequired, aboutApp: PropTypes.string };

export default Navbar;
